import { Formik, Form } from 'formik';
import { Button, Box } from '@chakra-ui/react';
import { ethers } from 'ethers';
import LazyMinting from '../contract-abi/LazyMinting.json';
import LazyMintingAddress from '../contract-abi/LazyMinting-address.json';
import toast, { Toaster } from 'react-hot-toast';
import { FormGroup, Input, InputGroup } from 'reactstrap';
import { useState } from 'react';
import { useParams } from 'react-router-dom'

interface MyFormValues {
  amount: number;
}

const ModuleFormik: any = () => {
  const initialValues: MyFormValues = { amount: 1 };
  const [title, setTitle] = useState();
  const [desc, setDesc] = useState();
  const params = useParams()


  const lazyMint = async () => {

    await window.ethereum.send('eth_requestAccounts')
    const provider = new ethers.providers.Web3Provider(window.ethereum)
    const signer = provider.getSigner()
    const address = await signer.getAddress()
    let tokenUrl = params.token
    let tokens = tokenUrl?.toString().split("=")[1]
    console.log(tokens)


    const signingDomain = async () => {
      const domain = {
        name: 'LazyMint-Voucher',
        version: '1',
        verifyingContract: LazyMintingAddress.address,
        chainId: 5, // put chain id here, on which chain you are going to deploy
      }
      return domain
    }
    const domain = await signingDomain()

    const types = {
      NFTVoucher: [
        { name: 'signerAddress', type: 'address' },
        { name: 'title', type: 'string' },
        { name: 'description', type: 'string' },
        { name: 'userId', type: 'string' },
      ],
    }

    const voucher = {
      signerAddress: address,
      title: title,
      description: desc,
      userId: tokens,
    }

    const signature = await signer._signTypedData(domain, types, voucher).then(() => {
      toast.success('Successful Mint');
    }).catch(()=>{
      toast.error('Mint Fail');
    })
    console.log(voucher, signature)
  };

  return (
    <>
      <div>
        <Formik
          initialValues={initialValues}
          onSubmit={() => {
            lazyMint();
          }}
        >
          <Form className="form">
            <FormGroup className='py-5'>
              <InputGroup>
                <Input
                  className="custom_form_feild"
                  name="title"
                  bsSize="lg"
                  placeholder="Enter Title"
                  onChange={(e: any) => {
                    setTitle(e.target.value);
                  }}
                />
              </InputGroup>
              <InputGroup>
                <Input
                  className="custom_form_feild my-3"
                  name="desc"
                  bsSize="lg"
                  placeholder="Enter Description"
                  onChange={(e: any) => {
                    setDesc(e.target.value);
                  }}
                />
              </InputGroup>
              <Box mt="0.5rem">
                <Button
                  className="btn-mint"
                  width="100%"
                  p="1.62rem"
                  type="submit"
                  borderRadius="1.25rem"
                  _hover={{ bg: 'rgb(255,165,0)' }}
                >
                  MINT NOW
                </Button>
              </Box>
            </FormGroup>
          </Form>
        </Formik>
        <Toaster
          position="top-right"
          toastOptions={{
            // Define default options
            className: '',
            duration: 3000,
            // Default options for specific types
            success: {
              duration: 3000,
            },
          }}
        />
      </div>
    </>
  );
};
export default ModuleFormik;
